import React, { Component } from 'react';

import MyLists from './MyLists';

import { Button, Icon, Col, notification, List } from 'antd';

const openNotification = () => {
    notification.open({
        message: 'Créer une liste de course',
        description: "Pour créer une liste, il vous suffit simplement d'écrire son nom dans la case \"ajouter une liste\" et de cliquer sur le +. Ensuite cliquez sur le nom de votre liste qui vient d'apparaitre en dessous. Vous pouvez en créer maximum 3.",
        duration: 0,
        style: {
            textAlign: 'justify'
          },
        // onClose: () => {
        //   alert('clicked');
        // },
      });
  };

class Home extends Component {
    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
    }

    liste = () => {
        const { listeDeBase } = this.props;
        const newListe = listeDeBase.filter(listeDeBase => listeDeBase !== 'user');
            return (
                <List
                    bordered
                    dataSource={newListe}
                    renderItem={ (item, id) =>
                        <List.Item >
                            <Col span={24} key={id} className='listeDeBase'>  
                                <Col xs={{ span: 20 }} md={{ span: 21 }} lg={{ span: 22 }} key={`elt-${item}`} id={item} onClick={e => this.props.choixListe(e)}>
                                    {item}
                                </Col>
                                <Col xs={{ span: 4 }} md={{ span: 3 }} lg={{ span: 2 }}>
                                    <Button 
                                        size='large' 
                                        name="trash" 
                                        type="primary"
                                        block 
                                        className="trash" 
                                        onClick={() => this.props.deleteList(item)}
                                    >  
                                        <Icon type='delete' style={{ color : '#009688', fontSize: '1.4em' }}/>
                                    </Button>
                                </Col>
                            </Col>
                        </List.Item>
                    }
                />
            )};
        
    render() {
        const {listeDeBase} = this.props;
        return (
            <>
            {
            !this.props.clicked ?
            <>
                <Col xs={{ span: 24 }} md={{ span: 22, push: 1 }} lg={{ span: 20, push: 2 }}>
                    { 
                        listeDeBase.length >= 4 ?
                        null
                        :
                        <form className='formulaire form-titre' onSubmit={e => this.props.addNewListTitre(e)}>
                            <input type='text' id='form-input' placeholder='Ajouter une liste' maxLength={30}/>
                            <button type='submit' id='myButtonSubmit'>+</button>
                        </form>
                    }
                
                    <Col>
                        {this.liste()}
                    </Col>
                    <Col span={6} style={{ marginRight: '1em' }}>
                        <Button block type="primary" onClick={this.props.returnToApp} className='goBack'>
                            <Icon type="left" />Retour
                        </Button>
                    </Col>
                </Col>
            </>
            :
            <Col span={24}>
                <MyLists
                    currentTitre={this.props.currentTitre}
                    createNewItem={this.props.createNewItem}
                    data={this.props.data}
                    isChecked={this.props.isChecked}
                    delete={this.props.delete}
                    toggleComment={this.props.toggleComment}
                    addComment={this.props.addComment}
                    titreListe={this.props.titreListe}
                    returnToApp={this.props.returnToApp}
                    closeComment={this.props.closeComment}
                    goHome={this.props.goHome}
                    indexItem={this.props.indexItem}
                    createNewCategory={this.props.createNewCategory}
                    isCategory={this.props.isCategory}
                    toggleSwitch={this.props.toggleSwitch}
                    numChildCategory={this.props.numChildCategory}
                    deleteCategory={this.props.deleteCategory}
                    getCurrentCategory={this.props.getCurrentCategory}
                    isToggleSwitchType={this.props.isToggleSwitchType}
                    toggleSwitchType={this.props.toggleSwitchType}
                    help={this.props.help}
                    inverseProduit={this.props.inverseProduit}
                    visible={this.props.visible}
                    onClose={this.props.onClose}
                    item={this.props.item}
                    modalContent={this.props.modalContent}
                    largeur={this.props.largeur}
                    itemIsVisible={this.props.itemIsVisible}
                    itemVisible={this.props.itemVisible}
                    handleScroll={this.props.handleScroll}
                    followWindow={this.props.followWindow}
                    showForm={this.props.showForm}
                    showFormulaire={this.props.showFormulaire}
                    currentCategory={this.props.currentCategory}
                    categoryVisible={this.props.categoryVisible}
                    categoryIsVisible={this.props.categoryIsVisible}
                    reloadList={this.props.reloadList}
                />
            </Col>
            }
            </>
        )
    }
}
 
export default Home;